import { Color, Component, Label, Layout, Node, Sprite, SpriteFrame, Toggle, UIOpacity, instantiate, path } from "cc";
export const skill_quality_color = [`#ffffff`, `#8AEF4A`, `#8AE9F5`, `#F188F1`, `#F8B034`, `#F17161`, `#ff0000`, `#00ff00`, `#0000ff`]
export class Pageitem extends Component {

    protected onLoad(): void {
      
    }
    /**
     * 刷新道具数据
     * @param data 
     */
    async SetData(data) {
        this.node.getComponent(Sprite).color = new Color().fromHEX(skill_quality_color[data]);
       
    }
}